.MatrixForm {
  width: 50%;
  margin:0 auto;
  text-align: center;
  margin-top:5px;
}
.MatrixForm label{
	padding-top: 5px;
	text-align: left;
	float: left;
	width:100%;
}
.MatrixForm input{
	clear: both;
	width: 100%;
	margin-bottom: 5px;
	margin-top: 5px;
	border: 1px solid darkgray;
	border-radius: 15px;
	text-align: center;
}
.MatrixForm input[type="submit"]{
	padding-top:10px;
	display: block;
	—Ålear: both;
	margin-top:10px;
	border: 0px;
	border-radius: 15px;
	padding: 10px;
	color: white;
	font-weight: bold;
	background: lightblue;
}

.MatrixForm-logo {
  -webkit-animation: MatrixForm-logo-spin infinite 20s linear;
          animation: MatrixForm-logo-spin infinite 20s linear;
  height: 80px;
}

.MatrixForm-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.MatrixForm-title {
  font-size: 1.5em;
}

.MatrixForm-intro {
  font-size: large;
}

@-webkit-keyframes MatrixForm-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes MatrixForm-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



